@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.movies__movie {
	position: relative;
	width: 48%;
	border: 1px solid #444;
	margin-bottom: 30px;
	transition: background 0.3s linear;
}

.movies__movie:nth-last-of-type(1),
.movies__movie:nth-last-of-type(2) {
	margin-bottom: 0;
}

.movies__movie:hover {
	background: #fff;
}

.movies__movie > a {
	display: flex;
	justify-content: space-around;
	align-items: flex-end;
	height: 100%;
	padding: 20px;
	color: #444;
	text-decoration: none;
}

.movie__poster {
	display: flex;
	justify-content: center;
	align-items: center;
	flex: 0 0 170px;
	padding: 5px;
	border: 1px solid #444;
}
.movie__poster > img {
	width: 100%;
}

.movie__data {
	flex: 2 1;
	margin-left: 20px;
}

.movie__title {
	width: 100%;
	font-size: 1.2rem;
	font-weight: 500;
}

.movie__year {
	font-weight: 400;
}

.genres {
	display: flex;
	justify-content: flex-start;
	align-items: center;
}

.genres__genre {
	list-style: none;
	font-size: 0.8rem;
}

.genres__genre:not(:last-child)::after {
	content: '|';
	margin: 0 5px;
}

.movie__summary {
	margin-top: 20px;
	/* display: -webkit-box;
	word-wrap: break-word;
	-webkit-line-clamp: 3;
	-webkit-box-orient: vertical;
	overflow: hidden;
	text-overflow: ellipsis; */
	/* height:54px; */
}

/* google font */

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Fira Sans", sans-serif;
}

html,
body,
#root,
section.container {
	background: #eee;
	color: #444;
	height: 100%;
}

.navigation {
	position: fixed;
	top: 1vw;
	left: 1vw;
	width: 30px;
	height: 30px;
}

.navigation > a {
	display: block;
	width: 100%;
	height: 100%;
	mask-image: url(/movie_app/static/media/icons8-home.ac4baa82.svg);
	-webkit-mask-image: url(/movie_app/static/media/icons8-home.ac4baa82.svg);
	mask-repeat: no-repeat;
	-webkit-mask-repeat: no-repeat;
	mask-position: center center;
	-webkit-mask-position: center center;
	mask-size: cover;
	-webkit-mask-size: cover;
	background: #444;
}

.loader {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	-webkit-animation: fade 0.8s alternate infinite linear;
	        animation: fade 0.8s alternate infinite linear;
}

@-webkit-keyframes fade {
	from {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}

@keyframes fade {
	from {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}

.movies {
	display: flex;
	/* height: 100%; */
	flex-wrap: wrap;
	justify-content: space-between;
	padding: 50px;
}

